.container{
    display: flex;
    align-items: center;
    justify-items: center;
    justify-content: center;

    margin-top: 200px;
    background-color: rgb(24, 25, 27);

}
.container label, .container input, .container form{
    background-color: #1e2738;
    color: azure;
}

.container form{
    padding: 50px;
    border-radius: 30px;
    display: 0 flex;
    align-items: center;
    justify-items: center;
    justify-content: center;
}
.container button {

    background-color: rgb(44, 44, 205);
    width: 100%;
}


