.header_container{
    background-color: rgb(13, 13, 13);
    height: 50px;
    top: 0;  
    z-index: 100;  
    width: 100%;     
    position: fixed;
    align-items: center;
    justify-items: center;
    justify-content: center;
    padding-top: 10px;
    font-size: 20px;

}

.header_container nav ul {
    list-style: none;
    display: flex;
    gap: 20px;
    align-items: center;
    justify-content: center;
}

.header_container nav ul li button {
    background-color: transparent;
    border: none;
    cursor: pointer;
}